.header-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  margin: auto;
  position: relative;
  height: calc(100vh - var(--headerHeight));
  overflow: hidden;
}
.header-section::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background: rgb(18, 29, 34);
  background: linear-gradient(90deg, rgb(18, 29, 34) 0%, rgb(18, 29, 34) 50%, rgba(18, 29, 34, 0.5018382353) 100%);
  z-index: -1;
}
.header-section .heading-wrap {
  display: flex;
  flex-direction: column;
  margin-bottom: 30px;
}
.header-section .heading-text {
  display: flex;
  max-width: 800px;
}
.header-section .header-image {
  position: absolute;
  top: 0;
  right: 0;
  width: 50%;
  height: 100%;
  object-fit: cover;
  z-index: -2;
}

#sub-menu-holder {
  width: 60%;
  height: calc(100vh - 100px);
  overflow: scroll;
}

#sub-menu {
  padding: 0 150px 30px 30px;
}
#sub-menu ul {
  list-style-type: none !important;
  padding-inline-start: 0px;
}
#sub-menu li.menu-item {
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: space-between;
  padding: 10px 20px;
}
#sub-menu li.menu-item:hover {
  background: #3A5A66;
}
#sub-menu li.menu-item a {
  max-width: 70%;
  margin-bottom: 10px;
}
#sub-menu li.menu-item a .description {
  color: #fff;
  font-weight: 400;
}

#sub-menu-holder {
  transition: right 0.3s ease;
}
#sub-menu-holder.open {
  right: 0;
  top: 100px;
}

/* MAIN MENU */
#main-nav {
  position: relative;
}
#main-nav ul {
  padding-inline-start: 0px;
  display: flex;
  margin-block-start: 0;
  margin-block-end: 0;
  position: relative;
}
#main-nav ul li {
  padding: 25px;
  min-height: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
}
#main-nav ul a {
  color: white;
}
#main-nav ul:hover {
  cursor: pointer;
}

.menu-inner {
  display: flex;
}

li.menu-item-has-children {
  list-style: none;
}

li.menu-item-has-children > a {
  text-transform: uppercase;
  font-size: 1.5rem;
  font-weight: 200;
}

li.menu-item-has-children:hover > a {
  border-bottom: 3px solid color(3);
}

/* SUB-MENU */
.sub-menu-wrapper {
  flex-direction: row;
  align-items: flex-end;
  background: #121d22;
  display: none;
  position: absolute;
  top: 100%;
  border-top: 1px solid white;
  right: 0;
  min-width: calc(100vw - 100px);
  min-height: 400px;
  height: 100%;
  width: 100%;
}
.sub-menu-wrapper .sub-menu {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  height: 100%;
  justify-content: space-evenly;
}
.sub-menu-wrapper .sub-menu li {
  display: flex;
  flex-direction: column;
  padding: 10px;
}
.sub-menu-wrapper .sub-menu li > a {
  color: white;
  font-size: 1.5rem;
  font-weight: 300;
  position: relative;
  display: flex;
  align-items: center;
  gap: 10px;
}
.sub-menu-wrapper .sub-menu li svg {
  transform: translateX(0%);
  transition: all 0.3s ease-out;
}
.sub-menu-wrapper .sub-menu .sub-menu-image-wrapper {
  display: none;
}

.sub-menu-image-wrapper {
  display: flex;
  width: 33%;
  height: 100%;
}
.sub-menu-image-wrapper img.sub-menu-image {
  width: 100%;
  display: block;
  object-fit: cover;
}

/* OVERLAY */
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  opacity: 0;
  visibility: hidden;
  z-index: 98;
  transition: all 0.3s ease;
}

.show {
  opacity: 1;
  visibility: visible;
}

/* HOVER */
.menu-inner li:hover .sub-menu-wrapper {
  display: flex;
}

li.menu-item-has-children > a:hover .overlay {
  opacity: 1;
  visibility: hidden;
}

.sub-menu li:hover .sub-menu-image {
  opacity: 1;
}

.sub-menu li.active .sub-menu-image {
  opacity: 1;
}

.sub-menu li:hover svg {
  opacity: 1;
  transform: translateX(150%);
}

#offer-news-slider .swiper {
  width: 100%;
  height: 100%;
}

#offer-news-slider .swiper-wrapper {
  align-items: stretch;
}

#offer-news-slider .swiper-slide {
  text-align: left;
  background: var(--blue-violet-gradient);
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 15px 25px;
  color: white;
  position: relative;
  height: auto;
  justify-content: space-between;
  border-radius: 3px;
}
#offer-news-slider:before {
  content: "";
  width: 100%;
  height: 100%;
  color: rgb(255, 255, 255);
  position: absolute;
  left: 0;
  top: 0;
  backdrop-filter: blur(3px);
  z-index: -1;
}
#offer-news-slider img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

#offer-news-slider .button {
  display: flex;
  flex-direction: row;
}

