.mitarbeiter {
  width: calc(100% - 60px);
  display: grid;
  grid-template-columns: repeat(3, 33.33%);
  gap: 20px;
  margin-top: 40px;
}
.mitarbeiter .mitarbeiter-bereich {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 30px 40px;
  background: #1D2D33;
  border-radius: 3px;
}
.mitarbeiter .teammitglied {
  background: #1D2D33;
  border-radius: 3px;
  position: relative;
}
.mitarbeiter .teammitglied .button-certificate {
  display: flex;
  align-items: center;
  justify-content: center;
  background: #74B5CC;
  border-radius: 50%;
  padding: 8px;
  width: 40px;
  height: 40px;
  position: absolute;
  right: 10px;
  top: 10px;
  font-size: 18px;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.2s ease;
}
.mitarbeiter .teammitglied .button-certificate:hover {
  transform: scale(1.1);
}
.mitarbeiter .teammitglied .teammitglied-text-wrapper {
  padding: 10px 30px 30px 30px;
}
.mitarbeiter .teammitglied .teammitglied-text-wrapper .teammitglied-text .position {
  font-size: 16px;
  color: #74B5CC;
  font-weight: 500;
}
.mitarbeiter .teammitglied .bild-team {
  position: relative;
}
.mitarbeiter .teammitglied .bild-team .buttons-team {
  display: flex;
  position: absolute;
  right: 10px;
  bottom: 20px;
}
.mitarbeiter .teammitglied .bild-team .buttons-team .contact-button {
  display: flex;
  align-items: center;
  justify-content: center;
  background: #74b5cc;
  border-radius: 50%;
  padding: 8px;
  width: 40px;
  height: 40px;
  background: #3A5A66;
  margin-left: 8px;
}
.mitarbeiter .teammitglied .bild-team .buttons-team .contact-button:hover {
  background: #74B5CC;
}
.mitarbeiter .teammitglied .bild-team .buttons-team .contact-button > img {
  height: 20px;
}
.mitarbeiter .teammitglied .bild-team > img {
  width: 100%;
  min-height: 280px;
  object-fit: cover;
}

@media (max-width: 1120px) {
  .mitarbeiter {
    width: calc(100% - 40px);
    grid-template-columns: repeat(2, 50%);
  }
}
@media (max-width: 786px) {
  .mitarbeiter {
    width: 100%;
    grid-template-columns: repeat(1, 100%);
  }
  .mitarbeiter .teammitglied-text h3 {
    font-size: 1.8rem;
  }
}
